@mixin d {
  @media only screen and (max-width: "1240px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(CarosSoft, CarosSoft);
@mixin f {
  font-family: 'CarosSoft', serif; }
@include font(CarosSoftLight, CarosSoftLight);
@mixin fl {
  font-family: 'CarosSoftLight', serif; }
@include font(CarosSoftMedium, CarosSoftMedium);
@mixin fm {
  font-family: 'CarosSoftMedium', serif; }
@include font(CarosSoftBold, CarosSoftBold);
@mixin fb {
  font-family: 'CarosSoftBold', serif; }

$orange: #EE5823;
$torq: #85C9D1;
$gray: #A7A7A7;
$purple: #3F2C4E;

$bg: #EEF6F8;
$border: #E7E7E7;

* {
 }  // box-shadow: 0 0 0 1px red

body {
  min-width: 375px;
  @include fl;
  font-size: 14px;
  line-height: 1.3;
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  @include fl;
  font-size: 14px; }
input {
  text-overflow: ellipsis; }
a {
  text-decoration: none; }

.out {
  overflow: hidden; }

.center {
  width: 100%;
  max-width: 1120px + 80px;
  margin: 0 auto;
  padding: 0 40px;
  @include t {
    padding: 0 30px; }
  @include m {
    padding: 0 17px; } }

.torq {
  color: $torq; }

.logo {
  display: block;
  width: 238px;
  height: 39px;
  background: url(../img/accomodation.co.uk.png) no-repeat 0 0 / contain;
  font-size: 0;
  @include m {
    width: 193px;
    height: 32px; } }

.social {
  &,
  &__list {
    display: flex;
    align-items: center; }
  &__title {
    margin-right: 35px;
    font-size: 12px;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    @include m {
      display: none; } }
  &__link {
    &:not(:last-child) {
      margin-right: 10px;
      @include m {
        margin-right: 30px; } }
    .icon {
      display: block;
      font-size: 22px;
      fill: white; } }
  &_color &__title {
    color: $orange; }
  &_color &__link {
    .icon {
      fill: $purple;
      @include m {
        fill: white; } } } }

.btn {
  display: inline-block;
  position: relative;
  padding: 16px 20px;
  background: $orange;
  border-radius: 23px;
  font-size: 11px;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  cursor: pointer;
  z-index: 4;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 13px;
    right: 13px;
    bottom: 0;
    border-radius: 23px;
    box-shadow: 0 10px 10px $orange;
    opacity: .2; }
  &_torq {
    background: $torq;
    &:before {
      box-shadow: 0 10px 10px $torq; } }
  &_white {
    background: white;
    color: $purple;
    &:before {
      box-shadow: none; } } }

.header {
  position: relative;
  padding: 52px 0;
  z-index: 20;
  @include m {
    padding: 17px 0; }
  &__center {
    display: flex;
    align-items: flex-start; }
  &__logo {
    margin-top: 4px;
    @include m {
      margin: 0 20px 0 0; } }
  &__social {
    margin-left: auto;
    @include m {
      margin-top: 3px; } } }

.footer {
  position: relative;
  padding: 50px 0 80px;
  background: $purple;
  color: white;
  z-index: 10;
  @include m {
    padding: 30px 0 50px; }
  &:before {
    content: '';
    position: absolute;
    top: 143px;
    left: 0;
    right: 0;
    height: 1px;
    background: rgba(#A5ADB5,.2);
    z-index: -4;
    @include m {
      top: 112px; } }
  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    background: url(../img/footer-shape.svg) no-repeat 0 0 / auto 100%, linear-gradient(to right, rgba(white,0) 350px, #382349 200px);
    z-index: -2; }
  &__center {
    display: flex;
    flex-wrap: wrap;
    @include m {
      flex-direction: column; } }
  &__subscribe {
    position: relative;
    max-width: 570px;
    margin: 0 40px 93px 0;
    flex: 1 0 50%;
    @include m {
      margin: 0 0 60px;
      flex: 0 0 100%;
      order: 1; } }
  &__input {
    width: 100%;
    height: 52px;
    padding: 0 60px 0 40px;
    border: 1px solid rgba(#A5ADB5,.2);
    border-radius: 26px;
    background: none;
    font-size: 12px;
    color: white;
    &::placeholder {
      color: rgba(white,.4); } }
  &__send {
    position: absolute;
    top: 0;
    right: 0;
    height: 52px;
    padding: 0 24px;
    background: none;
    font-size: 0;
    .icon {
      font-size: 12px;
      fill: $orange; } }
  &__logo {
    margin: 7px 0 0 auto;
    @include m {
      margin: 50px 0 20px;
      order: 3; } }
  &__row {
    display: flex;
    flex: 0 0 50%;
    @include t {
      flex: 0 0 50%; }
    @include m {
      justify-content: space-between;
      order: 2; } }
  &__col {
    &:not(:last-child) {
      margin-right: 87px;
      @include t {
        margin-right: 30px; } } }
  &__title {
    margin-bottom: 18px;
    @include fb;
    font-size: 14px;
    @include m {
      margin-bottom: 16px; } }
  &__link {
    display: table;
    margin-bottom: 8px;
    font-size: 12px;
    color: rgba(white,.7);
    @include m {
      margin-bottom: 18px; } }
  &__today {
    margin-left: auto;
    text-align: right;
    @include m {
      margin: 0;
      text-align: left;
      order: 4; } }
  &__head {
    margin-bottom: 44px;
    @include fb;
    font-size: 24px;
    @include m {
      margin-bottom: 25px;
      font-size: 18px; } }
  &__btn {
    min-width: 140px;
    margin-right: 13px;
    @include m {
      min-width: 160px;
      &:first-child {
        margin: 0 15px 15px 0; } } } }

.title {
  @include fb;
  font-size: 48px;
  color: $purple;
  line-height: (52/48);
  @include m {
    font-size: 24px;
    line-height: (28/24); }
  &_lg {
    font-size: 80px;
    line-height: 1;
    @include m {
      font-size: 40px;
      line-height: 1; } }
  &_sm {
    @include fm;
    font-size: 30px;
    line-height: 1.2;
    @include m {
      font-size: 16px;
      line-height: (19/16); } } }

.wave {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -68px;
  height: 186px;
  background: url(../img/wave.svg) no-repeat 0 0 / 100% 100%;
  pointer-events: none;
  @include d {
    bottom: 0;
    height: auto;
    background-size: 115% auto;
    &:before {
      content: '';
      display: block;
      height: 0;
      padding-bottom: 14%; } } }

.main {
  position: relative;
  min-height: 900px;
  margin-top: -147px;
  padding: 220px 0 200px;
  background: url(../img/bg-1.jpg) no-repeat 50% 50% / cover;
  color: white;
  overflow: hidden;
  @include m {
    min-height: 605px;
    margin-top: -66px;
    padding: 135px 0 105px; }
  &__content {
    max-width: 600px;
    margin-bottom: 95px;
    @include m {
      margin-bottom: 48px;
      text-align: center; } }
  &__title {
    margin-bottom: 22px;
    color: white;
    @include m {
      margin-bottom: 25px; } }
  &__text {
    max-width: 510px;
    font-size: 18px;
    line-height: (28/18);
    @include m {
      font-size: 14px;
      line-height: (20/14); } }
  &__subscribe {
    position: relative; }
  &__icon {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 62px;
    height: 64px;
    padding-left: 10px;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    @include m {
      height: 47px;
      padding: 0; }
    .icon {
      font-size: 11px;
      fill: $orange; } }
  &__input {
    width: 100%;
    height: 64px;
    padding: 0 218px 0 62px;
    border-radius: 40px;
    font-size: 18px;
    color: $gray;
    letter-spacing: .1px;
    @include m {
      height: 47px;
      margin-bottom: 13px;
      padding: 0 15px 0 50px;
      font-size: 12px; } }
  &__btn {
    position: absolute;
    top: 12px;
    right: 17px;
    width: 175px;
    height: 40px;
    padding: 10px;
    @include m {
      position: relative;
      top: 0;
      right: 0;
      width: 100%;
      height: 47px;
      font-size: 11px; } } }

.text {
  position: relative;
  padding-top: 50px;
  font-size: 14px;
  line-height: 1.2;
  color: $gray;
  @include m {
    padding-top: 40px;
    font-size: 12px;
    line-height: (15/12); }
  &:before {
    content: '';
    position: absolute;
    top: 21px;
    left: 0;
    width: 37px;
    height: 2px;
    background: $torq; }
  p {
    margin-bottom: 18px; }
  strong {
    @include fm; } }

.offer {
  padding: 40px 0 200px;
  background: white;
  @include t {
    padding-bottom: 100px; }
  @include m {
    padding: 20px 0 40px; }
  &__title {
    margin-bottom: 78px;
    text-align: center;
    @include m {
      margin-bottom: 50px; } }
  &__item &__center {
    display: flex;
    align-items: center;
    @include m {
      align-items: flex-start; } }
  &__content {
    padding: 25px 30px 0 13%;
    flex: 0 0 52%;
    @include t {
      padding-left: 50px; }
    @include m {
      padding: 10px 17px 0 0;
      flex: 0 0 56%; } }
  &__type {
    max-width: 360px; }
  &__preview {
    flex: 0 0 48%;
    text-align: center; }
  &__pic {
    max-width: 100%; }
  .owl {
    &-dots {
      position: absolute;
      top: 50%;
      right: 50%;
      margin-right: 542px;
      transform: translateY(-45%);
      @include d {
        right: auto;
        left: 40px;
        margin: 0; }
      @include t {
        left: 30px; }
      @include m {
        display: flex;
        position: static;
        margin: 115px 17px 0;
        justify-content: center;
        transform: none; } }
    &-dot {
      margin: 13px 0;
      padding: 8px;
      border-radius: 50%;
      @include m {
        margin: 0 7px; }
      &.active {
        background: $bg; }
      span {
        display: block;
        width: 11px;
        height: 11px;
        background: $torq;
        border-radius: 50%; } } } }

.field {
  margin-bottom: 15px;
  &_row {
    display: flex;
    align-items: center; }
  &_row &__title {
    margin-right: 25px; }
  &_row &__wrap {
    flex: 1 0 auto; }
  &__title {
    @include fb;
    font-size: 12px;
    color: $purple; }
  &__wrap {
    position: relative; }
  &__input,
  &__select {
    width: 100%;
    height: 47px;
    padding: 0 32px;
    border: 1px solid $border;
    border-radius: 47px;
    font-size: 12px;
    @include m {
      padding: 0 20px; } }
  &__select {
    background: white url(../img/arrow-select.svg) no-repeat calc(100% - 22px) 50% / 8px 6px;
    -webkit-appearance: none;
    cursor: pointer; }
  &__label {
    position: absolute;
    top: 16px;
    left: 28px;
    padding: 0 5px;
    background: white;
    font-size: 12px;
    color: #AFAFAF;
    transition: transform .2s; }
  &__input:focus + &__label,
  &__input.focus + &__label {
    transform: translateY(-23px); } }

.switch {
  display: block;
  position: relative;
  user-select: none;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__content {
    &:after {
      opacity: 1; } }
  &__content {
    display: block;
    position: relative;
    padding-left: 25px;
    @include fb;
    font-size: 12px;
    color: $gray;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 12px;
      height: 12px;
      margin-top: -7px;
      border: 1px solid #E4E6EC;
      border-radius: 50%; }
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 3px;
      width: 6px;
      height: 6px;
      margin-top: -4px;
      background: $orange;
      border-radius: 50%;
      opacity: 0;
      transition: opacity .2s; } } }

